.maxDiv-Control-App {
  display: grid;
  grid-template-columns: 100px 1fr;
}

.maxDiv-Control-App__Routes {
  width: 100%;
  height: 100%;
}

.navbar-user-container {
  width: 100px;
  height: 100vh;
}

.click_navbar-option:visited navbar-option {
  background-color: red;
}

.navbar-container {
  background-color: white;
  width: 100px;
  height: 100%;
  box-shadow: 4px 0px 15px rgb(209, 209, 209);
}

.navbar-brand {
  width: 100%;
  height: 55px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  position: -webkit-sticky;
  position: sticky;
  z-index: 2;
  box-shadow: -1px 3px 15px rgb(202, 202, 202);
}

.navbar-brand__icon {
  margin-left: 0.5em;
}

.navbar-brand__h5 {
  font-size: 1rem;
  /* color: #a8b0be; */
  color: rgb(133, 130, 130);
  margin-left: 0.7em;
  font-weight: 500;
  font-family: 'Ubuntu', sans-serif;
}

.clicked_navbar-option .navbar-option {
  background: linear-gradient(
    -37deg,
    rgba(34, 168, 153, 1),
    rgb(242, 243, 245) 9%
  );
}

.clicked_navbar-option .navbar-option__icon i {
  color: #22a899;
}

.clicked_navbar-option .navbar-option h5 {
  color: #22a899;
  font-weight: bold;
}

.navbar-option {
  width: 100%;
  height: 69px;
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 13%;
  cursor: pointer;
}

.navbar-option:hover {
  background: linear-gradient(
    -37deg,
    rgba(34, 168, 153, 1),
    rgba(248, 249, 251, 1) 9%
  );
}

.navbar-option__icon {
  display: grid;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: #a3b1ba;
}

.navbar-option:hover .navbar-option__icon i {
  color: #22a899;
  font-weight: bold;
}

.navbar-option__h5 {
  color: #a3b1ba;
  font-weight: 400;
  font-family: 'Ubuntu', sans-serif;
  font-size: 0.75rem;
}

.navbar-option:hover .navbar-option__h5 {
  color: #22a899;
  font-weight: 700;
}

/* USER SECTION */

.user-section {
  width: 100%;
  height: 8%;
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-top: 1px solid #bababa;
  /* border: 1px solid lightseagreen; */
}

.user-section__icon {
  display: grid;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 0.8em;
}

.user-section h4 {
  text-transform: capitalize ;
  margin-top: 0.5em;
}

.gg-user {
  display: block;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform: scale(var(--ggs, 1));
          transform: scale(var(--ggs, 1));
  box-sizing: border-box;
  width: 12px;
  height: 18px;
  
}
.gg-user::after,
.gg-user::before {
  content: '';
  display: block;
  box-sizing: border-box;
  position: absolute;
  border: 2px solid;
}
.gg-user::before {
  width: 8px;
  height: 8px;
  border-radius: 30px;
  top: 0;
  left: 2px;
}
.gg-user::after {
  width: 12px;
  height: 9px;
  border-bottom: 0;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  top: 9px;
}

/* Read Icon */

.gg-read,
.gg-read::after,
.gg-read::before {
    display: block;
    box-sizing: border-box;
    border: 2px solid
}
.gg-read {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-bottom-color: transparent;
    margin-left: -1px;
    position: relative;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform: scale(var(--ggs,1));
            transform: scale(var(--ggs,1));
    width: 6px;
    height: 5px;
    border-radius: 50px;
    color: #33a196;
    font-weight: 900;
}
.gg-read::after,
.gg-read::before {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 10px;
    left: -10px;
    top: -5px;
    box-shadow:
        -6px 0 0 -4px currentColor
}
.gg-read::after {
    left: 2px;
    box-shadow:
        6px 0 0 -4px currentColor
}

/* Chart-icon */
.gg-chart {
  box-sizing: border-box;
  position: relative;
  display: block;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform: scale(var(--ggs, 1));
          transform: scale(var(--ggs, 1));
  width: 10px;
  height: 10px;
  border-bottom: 2px solid;
  border-left: 2px solid;
}

.gg-chart::after,
.gg-chart::before {
  content: '';
  display: block;
  position: absolute;
  border: 2px solid;
  box-sizing: border-box;
}
.gg-chart::before {
  border-top-right-radius: 100px;
  right: -2px;
  top: -2px;
  width: 8px;
  height: 8px;
}
.gg-chart::after {
  width: 18px;
  height: 18px;
  border-radius: 100px;
  border-right-color: transparent;
  left: -10px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

/* Pen-icon: */
.gg-pen {
  box-sizing: border-box;
  position: relative;
  display: block;
  -webkit-transform: rotate(-45deg) scale(1);
          transform: rotate(-45deg) scale(1);
  -webkit-transform: rotate(-45deg) scale(var(--ggs, 1));
          transform: rotate(-45deg) scale(var(--ggs, 1));
  width: 14px;
  height: 4px;
  border-right: 2px solid transparent;
  box-shadow: 0 0 0 2px, inset -2px 0 0;
  border-top-right-radius: 1px;
  border-bottom-right-radius: 1px;
  margin-right: -2px;
}
.gg-pen::after,
.gg-pen::before {
  content: '';
  display: block;
  box-sizing: border-box;
  position: absolute;
}
.gg-pen::before {
  background: currentColor;
  border-left: 0;
  right: -6px;
  width: 3px;
  height: 4px;
  border-radius: 1px;
  top: 0;
}
.gg-pen::after {
  width: 8px;
  height: 7px;
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
  border-right: 7px solid;
  left: -11px;
  top: -2px;
}

/* Box icon */
.gg-box {
  box-sizing: border-box;
  position: relative;
  display: block;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform: scale(var(--ggs, 1));
          transform: scale(var(--ggs, 1));
  width: 22px;
  height: 20px;
  border: 2px solid;
  border-radius: 3px;
}
.gg-box::after,
.gg-box::before {
  content: '';
  display: block;
  box-sizing: border-box;
  position: absolute;
  border-radius: 3px;
  height: 2px;
  background: currentColor;
}
.gg-box::before {
  left: 6px;
  width: 6px;
  top: 8px;
}
.gg-box::after {
  width: 22px;
  left: -2px;
  top: 4px;
}

/* Shape half circle */
.gg-shape-half-circle {
  box-sizing: border-box;
  position: relative;
  display: block;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform: scale(var(--ggs, 1));
          transform: scale(var(--ggs, 1));
  width: 8px;
  height: 16px;
  border: 3px solid;
  border-top-left-radius: 100px;
  border-bottom-left-radius: 100px;
  border-right: 0;
}

/*gg-browse*/
.gg-browse {
  display: block;
  position: relative;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform: scale(var(--ggs, 1));
          transform: scale(var(--ggs, 1));
  box-sizing: border-box;
  width: 16px;
  height: 22px;
  border: 2px solid;
  border-radius: 3px;
  background: linear-gradient(to left, currentcolor 10px, transparent 0)
      no-repeat center 2px/8px 2px,
    linear-gradient(to left, currentcolor 10px, transparent 0) no-repeat center
      6px/8px 2px;
}
.gg-browse::after,
.gg-browse::before {
  content: '';
  display: block;
  box-sizing: border-box;
  position: absolute;
  border-radius: 22px;
}
.gg-browse::before {
  width: 6px;
  height: 6px;
  border: 2px solid;
  left: 3px;
  top: 9px;
}
.gg-browse::after {
  width: 2px;
  height: 4px;
  background: currentColor;
  bottom: 1px;
  right: 2px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

/*gg-home-alt*/

.gg-home-alt {
  box-sizing: border-box;
  position: relative;
  display: block;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform: scale(var(--ggs, 1));
          transform: scale(var(--ggs, 1));
  width: 18px;
  height: 14px;
  border: 2px solid;
  border-top: 0;
  border-radius: 2px;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  margin-bottom: -2px;
}
.gg-home-alt::after,
.gg-home-alt::before {
  content: '';
  display: block;
  box-sizing: border-box;
  position: absolute;
}
.gg-home-alt::before {
  border-top: 2px solid;
  border-left: 2px solid;
  border-top-left-radius: 4px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  top: -5px;
  border-radius: 3px;
  width: 14px;
  height: 14px;
  left: 0;
}
.gg-home-alt::after {
  width: 6px;
  height: 10px;
  background: currentColor;
  border-top-left-radius: 100px;
  border-top-right-radius: 100px;
  left: 4px;
  bottom: -2px;
}

/* add-r */
.gg-add-r {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 22px;
  height: 22px;
  border: 2px solid;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform: scale(var(--ggs, 1));
          transform: scale(var(--ggs, 1));
  border-radius: 4px;
}
.gg-add-r::after,
.gg-add-r::before {
  content: '';
  display: block;
  box-sizing: border-box;
  position: absolute;
  width: 10px;
  height: 2px;
  background: currentColor;
  border-radius: 5px;
  top: 8px;
  left: 4px;
}
.gg-add-r::after {
  width: 2px;
  height: 10px;
  top: 4px;
  left: 8px;
}

/* log-out icon */
.gg-log-out {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 6px;
  height: 16px;
  border: 2px solid;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform: scale(var(--ggs, 1));
          transform: scale(var(--ggs, 1));
  border-right: 0;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  margin-left: -10px;
}
.gg-log-out::after,
.gg-log-out::before {
  content: '';
  display: block;
  box-sizing: border-box;
  position: absolute;
}
.gg-log-out::after {
  border-top: 2px solid;
  border-left: 2px solid;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  width: 8px;
  height: 8px;
  left: 4px;
  bottom: 2px;
}
.gg-log-out::before {
  border-radius: 3px;
  width: 10px;
  height: 2px;
  background: currentColor;
  left: 5px;
  bottom: 5px;
}

/* RESPONSIVE */

@media (max-height: 780px) {
  .navbar-option {
    width: 100%;
    height: 55px;
    margin-bottom: 16%;
    cursor: pointer;
  }
}

@media screen and  (min-width: 1919px) {
  .user-section {
    margin-top: 11.5em;
    border: 0;
  }

  .navbar-user-container {
    width: 100px;
    height: 100vh;
  }

}
