.general-container-warehouse {
  width: 100%;
  height: 100vh;
}

.scroll-warehouse {
  height: 93vh;
  overflow: hidden;
  /* overflow-y: scroll; */
  scroll-behavior: smooth;
}

.scroll-warehouse::-webkit-scrollbar {
  background-color: #f8f9fb;
  width: 16px;
}

.scroll-warehouse::-webkit-scrollbar-track {
  background-color: #f8f9fb;
}

.scroll-warehouse::-webkit-scrollbar-thumb {
  background-color: #babac0;
  border-radius: 16px;
  border: 4px solid #f8f9fb;
}

.scroll-warehouse::-webkit-scrollbar-button {
  display: none;
}

.navbar-warehouse {
  height: 55px;
  width: 100%;
  background-color: transparent;
  position: sticky;
  z-index: 0;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 4px 4px 15px rgb(221, 221, 221);
}

.navbar-warehouse__h2 {
  color: black;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 600;
  font-size: 1.2rem;
  margin-left: 2em;
  margin-top: 0.7em;
}

.navbar-warehouse-otpions {
  width: 60%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 3em;
}


.general-container-warehouse__h2 {
  font-size: 1.1rem;
  font-family: 'Ubuntu', sans-serif;
  letter-spacing: normal;
  color: #374663;
  margin: 20px 0px 0px 37px;
}

.tabs-warehouse {
  /* border-bottom: 3px solid #00bfa6; */
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* background-color: #f5faf9; */
}

.tabs-warehouse a  {
  color: black;
  display: block;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.active-tabs-warehouse {
  border-bottom: 3px solid #00bfa6;
}

.active-tabs-warehouse a  {
  color: #00bfa6;
  font-weight: 600;
}

.general-container-warehouse__p {
  font-family: 'Ubuntu', sans-serif;
  font-size: 0.85rem;
  color: #9ba9af;
  margin: 12px 0px 0px 37px;
}

.body-warehouse-information {
  display: flex;
  /* border: 1px solid blue; */
  width: 100%;
  height: 94vh;
}

.warehouse-form-container {
  width: 40%;
  margin-top: 1%;
}

.warehouse-form {
  width: 90%;
  margin-left: 3em;
  margin-top: 4.5em;
  height: 75%;
  background-color: white;
  box-shadow: 4px 0px 15px rgb(235, 235, 235);
  border: 1px solid transparent;
}

.warehouse-form h2 {
  font-family: 'Ubuntu', sans-serif;
  font-size: 0.9rem;
  color: #374663;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3%;
}

.warehouse-form .border-div {
  border-bottom: 1.5px solid #374663;
  width: 90%;
  margin: 0px auto;
  margin-bottom: 4%;
}

.warehouse-form input {
  display: flex;
  margin: 0px auto;
  margin-bottom: 5%;
  width: 90%;
  height: 7%;
  font-family: 'Ubuntu', sans-serif;
  font-size: 0.75rem;
  padding-left: 3%;
  border: 1px solid #9ba9af;
  color: #9ba9af;
}

.warehouse-form input:focus {
  outline: none;
}

.warehouse-form .descriptionInput {
  padding-top: -20px;
  height: 10%;
}

.filterDropdownSupplieWarehouse {
  width: 90%;
  margin-left: 1.5em;
}

.select-container-bodega {
  width: 90%;
  margin: 15px auto;
  margin-top: -2px;
  position: relative;
  margin-bottom: 1em;
}

.selected-option-bodega {
  background: white;
  border-radius: 5px;
  padding: 5px 15px;
  cursor: pointer;
  border: 1px solid #9ba9af;
  font-family: 'Ubuntu', sans-serif;
  font-size: 0.75rem;
  width: 100%;
  height: 30px;
  color: #6f797c;
}

.selected-option-bodega::after {
  content: '';
  position: absolute;
  top: 10px;
  right: 20px;
  width: 8px;
  height: 8px;
  border-left: 3px solid #1da597;
  border-top: 3px solid #1da597;
  transform: rotate(-135deg);
  transition: all 0.4s ease-in-out;
}

.active-bodega .selected-option-bodega::after {
  top: 12px;
  transform: rotate(45deg);
}

.options-container-bodega {
  position: absolute;
  top: 32px;
  left: 0;
  width: 100%;
  background: white;
  border-radius: 5px;
  max-height: 240px;
  overflow-y: scroll;
  transform: rotateX(90deg);
  transform-origin: top;
  transition: transform 0.3s ease-in-out;
  color: #9ba9af;
  z-index: 20;
}

.options-container-bodega::-webkit-scrollbar {
  width: 10px;
  background: #e7f7f2;
  border-radius: 0 8px 8px 0;
}

.options-container-bodega::-webkit-scrollbar-thumb {
  background: #1da597;
  border-radius: 0 5px 5px 0;
}

.active-bodega .options-container-bodega {
  transform: rotateX(0);
}

.option-bodega {
  padding: 10px 20px;
  cursor: pointer;
  font-family: 'Ubuntu', sans-serif;
  font-size: 0.75rem;
}

.option-bodega:hover {
  background: #e7f7f2;
}

.warehouse-form button {
  width: 90%;
  margin-left: 5%;
  height: 7%;
  font-family: 'Ubuntu', sans-serif;
  font-size: 0.8rem;
  margin-top: -0.1%;
}

.warehouse-form button:focus {
  outline: none;
}

.information-add-supplies-container {
