  .information-add-supplies-container__h2 {
    font-family: 'Ubuntu', sans-serif;
    font-size: 2.1rem;
  }
  
  .information-add-supplies-container__p {
    font-family: 'Ubuntu', sans-serif;
    margin-top: 0.8em;
    font-size: 1.7rem;
    font-weight: 300;
    color: #000000;
    line-height: 2rem;
  }

  .update-container {
    width: 100%;
    height: 94vh;
  }

  .inventory-warehouse-open-modal {
    margin-top: 7.5%;
  }

  .update-container__h2 {
    margin-top: 1.2em;
    font-size: 1.7rem;
  }

  .update-container-card {
    margin-top: 17em;
    height: 33%;
  }

  .information-update-container {
    margin-top: 20.5em;
    margin-left: 4em;
  }

  .information-update-container__h2 {
    font-size: 2.2rem;
  }

  .information-update-container__p {
    font-size: 1.9rem;
  }

  .update-container-form button {
    height: 14%;
  }

  .warehouseReqSection {
    width: 100%;
    height: 94vh;
  }

  .requestWarehouseContainer {
    width: 25%;
    height: 25%;
    margin-left: 6%;
  }

  .requestWarehouseContainer__h4 {
    margin-top: 5%;
  }

  .requestWarehouseContainer__reference {
